<template>
    <div>
        <div v-if="todoList.length < 1" class="no_data">
            Îì±Î°ùÎêú Ìï†ÏùºÏù¥ ÏóÜÏäµÎãàÎã§.
        </div>
        <div class="content" v-else>
            <div class="sort">
                <select name="slct" id="#sort" class="slct" @change="chageSort($event.target.value,'slct')" v-model="slct">
                    <option value="regi">Îì±Î°ùÏàú</option>
                    <option value="new">ÏµúÏã†Ïàú</option>
                    <option value="abc">Í∞ÄÎÇòÎã§Ïàú</option>
                    <option value="comp">ÏôÑÎ£åÏàú</option>
                    <option value="ing">ÏßÑÌñâÏàú</option>
                    <option value="user">ÏÇ¨Ïö©ÏûêÎ™®Îìú</option>
                </select>
            </div>
            <draggable class="lst" ghost-class="ghost" v-model="list" @start="chageSort('user')" @end="updateList"> 
                <div class="unit" v-for="(item, i) in todoList" :key="item.key" v-bind:class="item.isDone?'done':''">
                    <div v-if="!item.isMode" class="view"  @click="toggleItem(item.key, i,item.isDone)">
                        <span class="txt">{{item.todo}}</span>
                        <button type="button" class="btn_modi"  @click="chageMode(item.key, i, item.isMode)">ÏàòÏ†ï</button>
                        <button type="button" class="btn_delete" @click="deleteItem(item.key, i)">ÏÇ≠Ï†ú</button>
                        <span class="date">
                            {{item.date}}
                        </span>
                    </div>
                    <div v-else class="modi">
                        <!-- form 2Í∞ú Ïù¥ÏÉÅ ÏùºÎïå Ìï¥Í≤∞ Î∞©Î≤ïÏùÑ Î™ªÏ∞æÏùå -->
                        <form @submit.prevent="modiItem(item.key, i)">
                          <input type="text" name="" id="" class="ipt" placeholder="Ìï†ÏùºÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!"  v-focus v-bind:value="item.todo" ref="todo_modi" >
                          <button type="button" class="btn_cancle" @click="chageMode(item.key, i, item.isMode)"><span>Ï∑®ÏÜå</span></button>
                          <button type="submit" class="btn_com" @click="modiItem(item.key, i)">ÏàòÏ†ï ÏôÑÎ£å</button>
                        </form>
                    </div>
                </div>
            </draggable>
            <p class="bx_total">Ï¥ù {{cnt.total}}Í∞ú, ÏôÑÎ£å {{cnt.comp}}Í∞ú, ÏßÑÌñâ {{cnt.ing}}Í∞ú</p>
            <button class="btn_reset" @click="resetAll">Reset</button>
        </div>
    </div>
</template>
<script>
import draggable from 'vuedraggable';

export default {
  data() {
    return {
      list:this.todoList,
      slct:this.sort,
    };
  },
  props: ['todoList', 'cnt', 'sort'],
  methods: {
    toggleItem($key, $idx, $isDone) {
      event.stopPropagation();
      // let state ="";
      $isDone = !($isDone);
      this.$emit('toggleItemEvent', $key, $idx, $isDone);
    },
    chageMode($key, $idx, $isMode) {
      event.stopPropagation();
      this.$emit('chageModeEvent', $key, $idx, $isMode);
    },
    modiItem($key, $idx) {
      event.stopPropagation();
      this.$emit('modiEvent', $key, $idx, this.$refs.todo_modi[0].value);
    },
    deleteItem($key, $idx) {
      event.stopPropagation();
      this.$emit('deleteEvent', $key, $idx);
    },
    resetAll() {
      event.stopPropagation();
      this.$emit('resetEvent');
    },
    chageSort($val, $mode) { 
      if($val == "user" && $mode=="slct"){
        alert("ÏÇ¨Ïö©Ïûê Î™®ÎìúÎäî ÎìúÎûòÍ∑∏Ïï§ÎìúÎûçÏãú ÏûêÎèô ÏÑ†ÌÉù Îê©ÎãàÎã§.");
        this.slct = this.sort;
      }
      else this.$emit('chageSortEvent', $val);
    },
    updateList(){
      this.$emit('updateListEvent', this.list);
      this.slct = this.sort
    }
  },
  components:{
    draggable
  },
};
</script>
